@use "base/variables" as v;

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
body {
  background-image: linear-gradient(to left, #434343 0%, black 100%);
  // background-color: v.$negro;
  font-size: 1.8rem;
  font-family: v.$fuente-principal;
  color: v.$blanco;
}
a {
  text-decoration: none;
}
img {
  width: 100%;
  height: auto;
}

h1 {
  text-transform: uppercase;
  text-align: center;
  margin: 10rem 0;
}

h2, h3 {
  text-align: center;
  margin: 5rem 0;
  text-transform: uppercase;
}
