@use "base/variables" as v;
@use "base/mixins" as m;

.contacto {
  @include m.contenedor;
  text-align: center;
  &__contenedor {
    @include m.tablet {
      display: flex;
      justify-content: center;
      max-width: 60rem;
      margin: 0 auto;
      align-items: center;
      gap: 3rem;
    }
  }
  i {
    color: v.$blanco;
    @include m.tablet {
      font-size: 5rem;
    }
  }
  &__telefono {
    @include m.tablet {
      display: flex;
      gap: 2rem;
      align-items: center;
    }
  }
}
