@use "base/variables" as v;
@use "base/mixins" as m;

.about {
  padding: 2rem 0;
  &__contenedor {
    @include m.contenedor;
    @include m.tablet {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 5rem;
    }
  }
  &__texto {
    line-height: 1.8;
    text-align: justify;
    &::after {
      @include m.tablet {
        display: block;
        content: "";
        background-color: red;
        width: 10rem;
        height: 0.3rem;
      }
    }
  }
}
