html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-image: linear-gradient(to left, #434343 0%, black 100%);
  font-size: 1.8rem;
  font-family: "Rubik", sans-serif;
  color: #ffffff;
}

a {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

h1 {
  text-transform: uppercase;
  text-align: center;
  margin: 10rem 0;
}

h2, h3 {
  text-align: center;
  margin: 5rem 0;
  text-transform: uppercase;
}

.header__contenedor {
  width: min(95%, 110rem);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__contenedor {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 2rem;
  }
}
.header__logo {
  padding: 2rem 0;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  border-bottom: 1px solid #ffffff;
  margin-bottom: 1rem;
}
.header__logo i {
  font-weight: 900;
  font-size: 2rem;
}
@media (min-width: 768px) {
  .header__logo {
    border-bottom: none;
    margin-bottom: 0;
  }
}
@media (min-width: 768px) {
  .header__nav {
    display: flex;
    gap: 2rem;
  }
}
.header__nav a {
  display: block;
  text-align: center;
  font-weight: 700;
  color: #ffffff;
  padding: 1rem;
  transition: ease-in-out;
  transition-duration: 300ms;
  transition-property: background-color, color;
  border-radius: 1rem;
  text-transform: uppercase;
}
.header__nav a:hover {
  background-color: #ffffff;
  color: #000000;
}

.footer {
  margin-top: 4rem;
}
.footer__copy {
  text-align: center;
  background-color: #000000;
  padding: 1rem 0;
}
.footer__copy p {
  font-size: 2rem;
  font-weight: 700;
  margin: 0;
}

.about {
  padding: 2rem 0;
}
.about__contenedor {
  width: min(95%, 110rem);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .about__contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.about__texto {
  line-height: 1.8;
  text-align: justify;
}
@media (min-width: 768px) {
  .about__texto::after {
    display: block;
    content: "";
    background-color: red;
    width: 10rem;
    height: 0.3rem;
  }
}

.certificaciones {
  padding: 2rem 0;
}
.certificaciones__contenedor {
  width: min(95%, 110rem);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .certificaciones__contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.certificaciones__texto {
  line-height: 1.8;
}

.webpages__contenedor {
  width: min(95%, 110rem);
  margin: 0 auto;
}
@media (min-width: 768px) {
  .webpages__contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
    gap: 5rem;
  }
}
.webpages__images {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .webpages__images {
    grid-template-columns: repeat(4, 1fr);
  }
}
.webpages__images-2 {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
.webpages__texto {
  text-align: justify;
  line-height: 1.5;
}
.webpages__texto h4 {
  margin: 3rem 0 0 0;
}

.button {
  width: 100%;
  margin: 2rem auto;
  display: block;
  padding: 1rem 3rem;
  border-radius: 2rem;
  border: 3px solid black;
  font-weight: bold;
  text-transform: uppercase;
  background-color: transparent;
  color: white;
  text-align: center;
  transition: ease 200ms;
}
.button:hover {
  color: black;
  background-color: white;
}

@media (min-width: 768px) {
  .button {
    width: auto;
    display: inline;
  }
}
.contacto {
  width: min(95%, 110rem);
  margin: 0 auto;
  text-align: center;
}
@media (min-width: 768px) {
  .contacto__contenedor {
    display: flex;
    justify-content: center;
    max-width: 60rem;
    margin: 0 auto;
    align-items: center;
    gap: 3rem;
  }
}
.contacto i {
  color: #ffffff;
}
@media (min-width: 768px) {
  .contacto i {
    font-size: 5rem;
  }
}
@media (min-width: 768px) {
  .contacto__telefono {
    display: flex;
    gap: 2rem;
    align-items: center;
  }
}