@use "base/variables" as v;
@use "base/mixins" as m;

.webpages {
  &__contenedor {
    @include m.contenedor;
    @include m.tablet {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      align-items: center;
      gap: 5rem;
    }
  }
  &__images {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    @include m.tablet {
      grid-template-columns: repeat(4, 1fr);
    }
  }
  &__images-2 {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
  &__texto {
    h4 {
      margin: 3rem 0 0 0;
    }
    text-align: justify;
    line-height: 1.5;
  }
}

.button {
  width: 100%;
  margin: 2rem auto;
  display: block;
  padding: 1rem 3rem;
  border-radius: 2rem;
  border: 3px solid black;
  font-weight: bold;
  text-transform: uppercase;
  background-color: transparent;
  color: white;
  text-align: center;
  transition: ease 200ms;
  &:hover{
    color: black;
    background-color: white;
  }
}

@include m.tablet {
  .button {
    width: auto;
    display: inline;
  }
}
