@use "base/variables" as v;
@use "base/mixins" as m;

.certificaciones {
  padding: 2rem 0;
  &__contenedor {
    @include m.contenedor;
    @include m.tablet {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 5rem;
    }
  }
  &__texto {
    line-height: 1.8;
  }
}
