@use "base/variables" as v;
@use "base/mixins" as m;

.header {
  &__contenedor {
    @include m.contenedor;
    @include m.tablet {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-top: 2rem;
    }
  }
  &__logo {
    padding: 2rem 0;
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    i {
      font-weight: v.$black;
      font-size: 2rem;
    }
    border-bottom: 1px solid v.$blanco;
    margin-bottom: 1rem;
    @include m.tablet {
      border-bottom: none;
      margin-bottom: 0;
    }
  }
  &__nav {
    @include m.tablet {
      display: flex;
      gap: 2rem;
    }
    a {
      display: block;
      text-align: center;
      font-weight: v.$bold;
      color: v.$blanco;
      padding: 1rem;
      transition: ease-in-out;
      transition-duration: 300ms;
      transition-property: background-color, color;
      border-radius: 1rem;
      text-transform: uppercase;
      &:hover {
        background-color: v.$blanco;
        color: v.$negro;
      }
    }
  }
}
