@use 'base/variables' as v;

@mixin contenedor {
  width: min(95%, 110rem);
  margin: 0 auto;
}

@mixin contenedor-md {
  width: min(95%, 80rem);
  margin: 0 auto;
}

@mixin contenedor-sm {
  width: min(95%, 60rem);
  margin: 0 auto;
}

@mixin tablet {
  @media (min-width: v.$tablet) {
    @content;
  }
}
